/* Base CSS reset and global styles */
:root {
  --background-dark: #1a1a1a;
  --text-green: #4CAF50;
  --text-white: #ffffff;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Import Press Start 2P font */
@import url('https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap');

/* Global styles - DESKTOP FIRST */
html, body, #root {
  background-color: #000000 !important;
  margin: 0;
  padding: 0;
  color: white;
  min-height: 100%;
  width: 100%;
  font-family: 'Press Start 2P', cursive;
  overflow-x: hidden;
}

/* App container styling - Desktop default */
.app-container {
  min-height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  z-index: 1;
}

/* Ensure content is visible against background */
.home {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2;
}

/* Ensure sections are visible - Desktop default */
section {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  z-index: 2;
}

/* Only apply these styles for tablets and below */
@media (max-width: 992px) {
  :root {
    --text-heading-1-font-size: 36px;
    --text-heading-2-font-size: 28px;
    --text-heading-3-font-size: 20px;
    --text-body-text-font-size: 16px;
  }
  
  section {
    max-width: 90%;
  }
}

/* Only apply these styles for medium tablets and below */
@media (max-width: 768px) {
  :root {
    --text-heading-1-font-size: 30px;
    --text-heading-2-font-size: 24px;
    --text-heading-3-font-size: 18px;
    --text-body-text-font-size: 14px;
  }
  
  .projects-container {
    flex-direction: column;
    align-items: center;
  }

  .project-card {
    width: 100%;
    max-width: 400px;
  }
}

/* Only apply these styles for mobile */
@media (max-width: 480px) {
  :root {
    --text-heading-1-font-size: 24px;
    --text-heading-2-font-size: 20px;
    --text-heading-3-font-size: 16px;
    --text-body-text-font-size: 12px;
  }
  
  section {
    padding: 10px;
  }
  
  .projects-overview {
    padding: 1rem;
  }

  .project-card .icon {
    width: 200px;
    height: 200px;
  }
}

body {
  width: 100%; 
  min-width: auto;
  background-color: #000000 !important; /* Ensure black background */
}

/* Force desktop view on large screens */
@media (min-width: 993px) {
  .mobile-only {
    display: none !important;
  }
}

/* Force mobile view on small screens */
@media (max-width: 992px) {
  .desktop-only {
    display: none !important;
  }
}