.HEADER {
  background-color: #000000;
  height: 80px; /* Taller for desktop */
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  border-bottom: 1px solid #0073B1;
  box-shadow: 0 0 10px rgba(0, 115, 177, 0.5);
  position: relative;
}

.nav-container {
  width: 100%;
  max-width: 1200px; /* Wider for desktop */
  display: flex;
  justify-content: center;
  align-items: center;
}

.nav-item {
  color: #FFFFFF;
  font-family: 'Press Start 2P', monospace;
  font-size: 16px; /* Larger for desktop */
  text-decoration: none;
  padding: 0 25px; /* More spacing for desktop */
  text-align: center;
  transition: color 0.3s ease;
}

.nav-item:hover, 
.nav-item.active {
  color: #589E00;
}

/* Tablet devices */
@media screen and (max-width: 992px) {
  .HEADER {
    height: 70px;
  }
  
  .nav-container {
    max-width: 800px;
  }
  
  .nav-item {
    font-size: 14px;
    padding: 0 20px;
  }
}

/* Small tablets and large phones */
@media screen and (max-width: 768px) {
  .HEADER {
    height: 60px;
  }
  
  .nav-container {
    padding: 0 10px;
    justify-content: space-around;
  }
  
  .nav-item {
    font-size: 11px;
    padding: 0 10px;
  }
}

/* Mobile phones */
@media screen and (max-width: 480px) {
  .HEADER {
    height: auto;
    padding: 10px 0;
  }
  
  .nav-container {
    flex-wrap: wrap;
    justify-content: center;
  }
  
  .nav-item {
    font-size: 9px;
    margin: 5px;
    padding: 0 5px;
  }
}