html, body {
  height: 100%;
  width: 100%;
}

.page-container {
  width: 100%;
  min-height: 100vh;
  background-color: transparent;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  height: auto;
}

.home {
  width: 100%;
  display: flex;
  justify-content: center;
  padding: 2rem;
  height: auto;
}

.content-wrapper {
  width: 100%;
  max-width: 1200px;
  margin: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  height: auto;
}

.about-me-container {
  width: 100%;
  display: flex;
  justify-content: center;
  margin: 60px 0;
  position: relative;
}

.about-me-content {
  width: 100%;
  max-width: 1000px;
  display: flex;
  align-items: center;
  gap: 60px;
  padding: 0 30px;
}

/* Green circle */
.profile-circle {
  width: 250px;
  height: 250px;
  background-color: #589E00;
  border-radius: 100%;
  flex-shrink: 0;
  box-shadow: inset 0 0 15px rgba(0, 0, 0, 0.5);
  border: 4px solid #466D00;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Profile image */
.profile-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 50%;
  border: 2px solid black;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.7);
}

/* Profile placeholder text */
.profile-placeholder {
  color: white;
  text-align: center;
  font-family: 'Press Start 2P', monospace;
  font-size: 16px;
  padding: 25px;
  line-height: 2;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5);
}

/* Text content */
.about-text {
  flex-grow: 1;
}

.about-heading {
  color: #589E00;
  font-family: var(--text-heading-1-font-family);
  font-size: var(--text-heading-1-font-size);
  font-style: var(--text-heading-1-font-style);
  font-weight: var(--text-heading-1-font-weight);
  margin-bottom: 30px;
  text-transform: uppercase;
  letter-spacing: var(--text-heading-1-letter-spacing);
  line-height: var(--text-heading-1-line-height);
  text-align: left;
}

.about-paragraph {
  color: white;
  font-family: var(--text-body-text-font-family);
  font-size: var(--text-body-text-font-size);
  font-style: var(--text-body-text-font-style);
  font-weight: var(--text-body-text-font-weight);
  line-height: calc(var(--text-body-text-line-height) * 1.2);
  text-align: left;
  position: relative;
  width: 100%;
  max-width: 650px;
}

.about-paragraph p {
  margin-bottom: 20px;
}

/* TABLET - Only apply these styles for tablets */
@media screen and (max-width: 992px) {
  .content-wrapper {
    max-width: 900px;
  }
  
  .about-me-content {
    max-width: 800px;
    gap: 50px;
    padding: 0 25px;
  }
  
  .profile-circle {
    width: 220px;
    height: 220px;
  }
  
  .profile-placeholder {
    font-size: 15px;
    padding: 22px;
  }
  
  .about-heading {
    margin-bottom: 25px;
  }
  
  .about-paragraph {
    max-width: 600px;
  }
  
  .about-paragraph p {
    margin-bottom: 18px;
  }
}

/* SMALLER TABLETS - Apply these styles for smaller tablets */
@media screen and (max-width: 768px) {
  .about-me-content {
    flex-direction: column;
    align-items: center;
    text-align: center;
    gap: 20px;
    padding: 0 20px;
  }
  
  .about-me-container {
    margin: 40px 0;
  }
  
  .profile-circle {
    width: 180px;
    height: 180px;
    border-width: 3px;
  }
  
  .about-heading {
    text-align: center;
    margin-bottom: 20px;
  }
  
  .about-paragraph {
    text-align: center;
    max-width: 100%;
  }
}

/* MOBILE - Apply these styles for mobile phones */
@media screen and (max-width: 480px) {
  .about-me-container {
    margin: 20px 0;
  }
  
  .about-me-content {
    padding: 0 10px;
    gap: 15px;
  }
  
  .profile-circle {
    width: 120px;
    height: 120px;
    border-width: 2px;
  }
  
  .profile-placeholder {
    font-size: 12px;
    padding: 15px;
    line-height: 1.6;
  }
  
  .about-heading {
    margin-bottom: 15px;
  }
  
  .about-paragraph p {
    margin-bottom: 10px;
  }
}

