/* Base layout */
.page-container {
    width: 100%;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #000000 !important;
    padding: 0;
    margin: 0;
    overflow-x: hidden; /* Prevent horizontal scrolling */
}

.home {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #000000 !important;
    overflow-x: hidden; /* Prevent horizontal scrolling */
}

.content-wrapper {
    width: 100%;
    max-width: 800px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
    padding: 0 20px;
    background-color: #000000;
    box-sizing: border-box; /* Ensure padding is included in width */
}

/* Section styling */
.section {
    width: 100%;
    margin: 40px 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #000000;
}

/* Ensure all section content is centered */
.section > div {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #000000;
}

/* Special section styling */
.header-section {
    width: 100%;
    max-width: none;
    margin: 0 0 30px 0;
    background-color: #000000;
    position: sticky;
    top: 0;
    z-index: 100;
}

.footer-section {
    margin-top: 40px;
    width: 100%;
    display: flex;
    justify-content: center;
    background-color: #000000;
}

/* Responsive styling */
@media screen and (max-width: 840px) {
    .content-wrapper {
        width: 95%;
        padding: 0 10px;
    }
    
    .section {
        margin: 30px 0;
    }
}

@media screen and (max-width: 480px) {
    .content-wrapper {
        padding: 0 5px;
    }
    
    .section {
        margin: 20px 0;
    }
    
    .header-section {
        margin-bottom: 15px;
    }
}

/* Global responsive text sizing */
@media screen and (max-width: 768px) {
    :root {
        --text-heading-1-font-size: 30px;
        --text-heading-2-font-size: 24px;
        --text-heading-3-font-size: 18px;
        --text-body-text-font-size: 14px;
    }
}

@media screen and (max-width: 480px) {
    :root {
        --text-heading-1-font-size: 24px;
        --text-heading-2-font-size: 20px;
        --text-heading-3-font-size: 16px;
        --text-body-text-font-size: 12px;
    }
}
  