.EXPERIENCE {
  background-color: transparent !important;
  height: auto;
  position: relative;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

.EXPERIENCE .EXCO {
  height: auto;
  position: relative;
  width: 100%;
  max-width: 1000px;
  margin: 0 auto 80px auto;
  padding: 30px;
  z-index: 15;
  background-color: transparent !important;
}

.EXPERIENCE .overlap-group {
  height: auto;
  position: relative;
  width: 100%;
  margin-bottom: 30px;
  background-color: transparent !important;
}

.EXPERIENCE .text-wrapper {
  color: var(--textsecondary-text);
  font-family: var(--text-body-text-font-family);
  font-size: calc(var(--text-body-text-font-size) * 1.1);
  font-style: var(--text-body-text-font-style);
  font-weight: var(--text-body-text-font-weight);
  letter-spacing: var(--text-body-text-letter-spacing);
  line-height: var(--text-body-text-line-height);
  margin-top: 15px;
  display: block;
  white-space: normal;
}

.EXPERIENCE .div {
  color: var(--mainprimary);
  font-family: var(--text-heading-3-font-family);
  font-size: calc(var(--text-heading-3-font-size) * 1.1);
  font-style: var(--text-heading-3-font-style);
  font-weight: var(--text-heading-3-font-weight);
  letter-spacing: var(--text-heading-3-letter-spacing);
  line-height: var(--text-heading-3-line-height);
  position: relative;
  text-align: left;
  white-space: normal;
  z-index: 20;
  margin-bottom: 15px;
}

.EXPERIENCE .p {
  color: var(--textprimary-text);
  font-family: var(--text-body-text-font-family);
  font-size: var(--text-body-text-font-size);
  font-style: var(--text-body-text-font-style);
  font-weight: var(--text-body-text-font-weight);
  letter-spacing: var(--text-body-text-letter-spacing);
  line-height: calc(var(--text-body-text-line-height) * 1.1);
  position: relative;
  width: 100%;
  z-index: 20;
  background-color: transparent !important;
}

.EXPERIENCE .BB-TECHNOSOL {
  height: auto;
  position: relative;
  width: 100%;
  max-width: 1000px;
  margin: 0 auto 80px auto;
  padding: 30px;
}

.EXPERIENCE .i-co-founded-b-b {
  color: var(--textprimary-text);
  font-family: var(--text-body-text-font-family);
  font-size: var(--text-body-text-font-size);
  font-style: var(--text-body-text-font-style);
  font-weight: var(--text-body-text-font-weight);
  letter-spacing: var(--text-body-text-letter-spacing);
  line-height: calc(var(--text-body-text-line-height) * 1.1);
  position: relative;
  text-align: left;
  width: 100%;
  margin-top: 25px;
}

.EXPERIENCE .overlap {
  height: auto;
  position: relative;
  width: 100%;
  margin-bottom: 30px;
}

.EXPERIENCE .b-b-TECHNOSOLUTIONS {
  color: var(--mainprimary);
  font-family: var(--text-heading-3-font-family);
  font-size: calc(var(--text-heading-3-font-size) * 1.1);
  font-style: var(--text-heading-3-font-style);
  font-weight: var(--text-heading-3-font-weight);
  letter-spacing: var(--text-heading-3-letter-spacing);
  line-height: var(--text-heading-3-line-height);
  position: relative;
  text-align: left;
  white-space: normal;
}

.EXPERIENCE .text-wrapper-3 {
  color: var(--textsecondary-text);
  font-family: var(--text-body-text-font-family);
  font-size: calc(var(--text-body-text-font-size) * 1.1);
  font-style: var(--text-body-text-font-style);
  font-weight: var(--text-body-text-font-weight);
  letter-spacing: var(--text-body-text-letter-spacing);
  line-height: var(--text-body-text-line-height);
  position: relative;
  text-align: left;
  white-space: normal;
  margin-top: 15px;
}

.EXPERIENCE .c-f-CROZIER {
  height: auto;
  position: relative;
  width: 100%;
  max-width: 1000px;
  margin: 0 auto 80px auto;
  padding: 30px;
}

.EXPERIENCE .overlap-2 {
  height: auto;
  position: relative;
  width: 100%;
  margin-bottom: 30px;
}

.EXPERIENCE .GUELPH-BRADFORD-JAN {
  color: var(--textsecondary-text);
  font-family: var(--text-body-text-font-family);
  font-size: calc(var(--text-body-text-font-size) * 1.1);
  font-style: var(--text-body-text-font-style);
  font-weight: var(--text-body-text-font-weight);
  letter-spacing: var(--text-body-text-letter-spacing);
  line-height: var(--text-body-text-line-height);
  position: relative;
  text-align: left;
  white-space: normal;
  margin-top: 15px;
}

.EXPERIENCE .during-my-time-at {
  color: var(--textprimary-text);
  font-family: var(--text-body-text-font-family);
  font-size: var(--text-body-text-font-size);
  font-style: var(--text-body-text-font-style);
  font-weight: var(--text-body-text-font-weight);
  letter-spacing: var(--text-body-text-letter-spacing);
  line-height: calc(var(--text-body-text-line-height) * 1.1);
  position: relative;
  width: 100%;
  margin-top: 25px;
}

.EXPERIENCE .text-wrapper-4 {
  color: var(--mainprimary-darker);
  font-family: var(--text-heading-1-font-family);
  font-size: calc(var(--text-heading-1-font-size) * 1.05);
  font-style: var(--text-heading-1-font-style);
  font-weight: var(--text-heading-1-font-weight);
  letter-spacing: var(--text-heading-1-letter-spacing);
  line-height: var(--text-heading-1-line-height);
  position: relative;
  text-align: center;
  white-space: normal;
  margin: 30px 0 60px 0;
  width: 100%;
}

/* Tablet styles - apply only when screen is smaller than 992px */
@media screen and (max-width: 992px) {
  .EXPERIENCE {
    max-width: 900px;
  }
  
  .EXPERIENCE .EXCO,
  .EXPERIENCE .BB-TECHNOSOL,
  .EXPERIENCE .c-f-CROZIER {
    max-width: 800px;
    margin-bottom: 70px;
    padding: 25px;
  }
  
  .EXPERIENCE .text-wrapper,
  .EXPERIENCE .text-wrapper-3,
  .EXPERIENCE .GUELPH-BRADFORD-JAN {
    font-size: var(--text-body-text-font-size);
    margin-top: 12px;
  }
  
  .EXPERIENCE .div,
  .EXPERIENCE .b-b-TECHNOSOLUTIONS {
    font-size: var(--text-heading-3-font-size);
    margin-bottom: 12px;
  }
  
  .EXPERIENCE .text-wrapper-4 {
    font-size: var(--text-heading-1-font-size);
    margin: 25px 0 50px 0;
  }
  
  .EXPERIENCE .overlap-group,
  .EXPERIENCE .overlap,
  .EXPERIENCE .overlap-2 {
    margin-bottom: 25px;
  }
  
  .EXPERIENCE .i-co-founded-b-b,
  .EXPERIENCE .during-my-time-at {
    margin-top: 20px;
    line-height: var(--text-body-text-line-height);
  }
  
  .EXPERIENCE .p {
    line-height: var(--text-body-text-line-height);
  }
}

/* Smaller tablet styles - apply only when screen is smaller than 768px */
@media screen and (max-width: 768px) {
  .EXPERIENCE {
    padding: 0 15px;
  }
  
  .EXPERIENCE .EXCO,
  .EXPERIENCE .BB-TECHNOSOL,
  .EXPERIENCE .c-f-CROZIER {
    padding: 20px;
    margin-bottom: 50px;
    max-width: 100%;
  }
  
  .EXPERIENCE .div,
  .EXPERIENCE .b-b-TECHNOSOLUTIONS {
    font-size: 20px;
    line-height: 26px;
    margin-bottom: 10px;
  }
  
  .EXPERIENCE .text-wrapper,
  .EXPERIENCE .text-wrapper-3,
  .EXPERIENCE .GUELPH-BRADFORD-JAN {
    font-size: 16px;
    margin-top: 10px;
  }
  
  .EXPERIENCE .p,
  .EXPERIENCE .i-co-founded-b-b,
  .EXPERIENCE .during-my-time-at {
    font-size: 16px;
    line-height: 22px;
    margin-top: 15px;
  }
  
  .EXPERIENCE .text-wrapper-4 {
    font-size: 28px;
    margin: 15px 0 35px 0;
  }
  
  .EXPERIENCE .overlap-group,
  .EXPERIENCE .overlap,
  .EXPERIENCE .overlap-2 {
    margin-bottom: 20px;
  }
}

/* Mobile styles - apply only when screen is smaller than 480px */
@media screen and (max-width: 480px) {
  .EXPERIENCE {
    padding: 0 10px;
  }
  
  .EXPERIENCE .EXCO,
  .EXPERIENCE .BB-TECHNOSOL,
  .EXPERIENCE .c-f-CROZIER {
    padding: 15px;
    margin-bottom: 30px;
  }
  
  .EXPERIENCE .div,
  .EXPERIENCE .b-b-TECHNOSOLUTIONS {
    font-size: 18px;
    line-height: 24px;
  }
  
  .EXPERIENCE .text-wrapper,
  .EXPERIENCE .text-wrapper-3,
  .EXPERIENCE .GUELPH-BRADFORD-JAN {
    font-size: 14px;
  }
  
  .EXPERIENCE .p,
  .EXPERIENCE .i-co-founded-b-b,
  .EXPERIENCE .during-my-time-at {
    font-size: 14px;
    line-height: 20px;
    margin-top: 12px;
  }
  
  .EXPERIENCE .text-wrapper-4 {
    font-size: 22px;
    margin: 10px 0 25px 0;
  }
  
  .EXPERIENCE .overlap-group,
  .EXPERIENCE .overlap,
  .EXPERIENCE .overlap-2 {
    margin-bottom: 15px;
  }
}