.FOOTER {
  background-color: var(--background-base-coloursdark-gray);
  height: 150px;
  position: absolute;
  left: -0px;
  top: 5000px;
  width: 350%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.FOOTER .text-wrapper {
  color: var(--mainprimary);
  font-family: var(--text-heading-3-font-family);
  font-size: var(--text-heading-3-font-size);
  font-style: var(--text-heading-3-font-style);
  font-weight: var(--text-heading-3-font-weight);
  letter-spacing: var(--text-heading-3-letter-spacing);
  line-height: var(--text-heading-3-line-height);
  position: absolute;
  white-space: nowrap;
  top: 42px;
  left: 50%;
  transform: translateX(-200px);
  text-align: center;
}


.FOOTER .email {
  height: 40px;
  position: absolute;
  top: 78px;
  width: 40px;
  left: 47.9%;
  transform: translateX(-40px);
}

.FOOTER .linkedin {
  height: 40px;
  position: absolute;
  top: 78px;
  width: 40px;
  left: 48%;
  transform: translateX(5px);
}

.FOOTER .github {
  height: 40px;
  position: absolute;
  top: 75px;
  width: 40px;
  left: 48.1%;
  transform: translateX(50px);
}