html, body {
    height: 100%;
    width: 100%;
  }

/* DESKTOP-FIRST STYLES */
.RESUME {
  background-color: transparent;
  height: auto;
  position: relative;
  width: 100%;
  max-width: 1200px; /* Wider for desktop */
  margin: 60px auto; /* More spacing for desktop */
  display: flex;
  flex-direction: column;
  align-items: center;
}

.RESUME .techImage {
  height: 220px; /* Larger for desktop */
  width: 240px; /* Larger for desktop */
  position: relative;
  margin: 30px 0; /* More spacing for desktop */
  transition: transform 0.3s ease;
  cursor: pointer;
  display: block;
}

.RESUME .downloadImage {
  height: 220px; /* Larger for desktop */
  width: 220px; /* Larger for desktop */
  position: relative;
  margin: 30px 0; /* More spacing for desktop */
  transition: transform 0.3s ease, opacity 0.3s ease;
  cursor: pointer;
  display: block;
}

.RESUME .downloadImage:hover,
.RESUME .techImage:hover {
  transform: scale(1.1);
  opacity: 0.8;
}

.RESUME .downloadImage:active,
.RESUME .techImage:active {
  transform: scale(0.95);
}

.RESUME .text-wrapper {
  color: var(--mainprimary);
  font-family: var(--text-heading-1-font-family);
  font-size: calc(var(--text-heading-1-font-size) * 1.05); /* Larger for desktop */
  font-style: var(--text-heading-1-font-style);
  font-weight: var(--text-heading-1-font-weight);
  letter-spacing: var(--text-heading-1-letter-spacing);
  line-height: var(--text-heading-1-line-height);
  position: relative;
  text-align: center;
  margin: 30px 0 50px 0; /* More spacing for desktop */
  white-space: normal;
  width: 100%;
}

.RESUME .resume-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 60px; /* More spacing for desktop */
  width: 100%;
  padding: 0 30px; /* More padding for desktop */
}

.RESUME .resume-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 450px; /* Wider for desktop */
  margin-bottom: 50px; /* More spacing for desktop */
}

.RESUME .overlap-group,
.RESUME .overlap {
  height: auto;
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.RESUME .TECH-RESUME,
.RESUME .CIVIL-PROJ {
  height: auto;
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 30px; /* More spacing for desktop */
}

.RESUME .div,
.RESUME .text-wrapper-2 {
  color: var(--mainprimary-lighter);
  font-family: var(--text-heading-3-font-family);
  font-size: calc(var(--text-heading-3-font-size) * 1.1); /* Larger for desktop */
  font-style: var(--text-heading-3-font-style);
  font-weight: var(--text-heading-3-font-weight);
  letter-spacing: var(--text-heading-3-letter-spacing);
  line-height: var(--text-heading-3-line-height);
  position: relative;
  text-align: center;
  margin-bottom: 20px; /* More spacing for desktop */
  width: 100%;
}

.RESUME .p {
  color: var(--textprimary-text);
  font-family: var(--text-body-text-font-family);
  font-size: calc(var(--text-body-text-font-size) * 1.05); /* Larger for desktop */
  font-style: var(--text-body-text-font-style);
  font-weight: var(--text-body-text-font-weight);
  letter-spacing: var(--text-body-text-letter-spacing);
  line-height: calc(var(--text-body-text-line-height) * 1.1); /* Increased line height for desktop */
  position: relative;
  text-align: center;
  width: 100%;
  margin-top: 25px; /* More spacing for desktop */
}

/* TABLET - Apply these styles for tablets */
@media screen and (max-width: 992px) {
  .RESUME {
    max-width: 900px;
    margin: 50px auto;
  }
  
  .RESUME .text-wrapper {
    font-size: var(--text-heading-1-font-size);
    margin: 25px 0 40px 0;
  }
  
  .RESUME .resume-container {
    gap: 50px;
    padding: 0 25px;
  }
  
  .RESUME .resume-card {
    max-width: 400px;
    margin-bottom: 40px;
  }
  
  .RESUME .techImage,
  .RESUME .downloadImage {
    height: 200px;
    width: 200px;
    margin: 25px 0;
  }
  
  .RESUME .TECH-RESUME,
  .RESUME .CIVIL-PROJ {
    margin-bottom: 25px;
  }
  
  .RESUME .div,
  .RESUME .text-wrapper-2 {
    font-size: var(--text-heading-3-font-size);
    margin-bottom: 18px;
  }
  
  .RESUME .p {
    font-size: var(--text-body-text-font-size);
    line-height: var(--text-body-text-line-height);
    margin-top: 22px;
  }
}

/* SMALLER TABLETS - Apply these styles for smaller tablets */
@media screen and (max-width: 768px) {
  .RESUME {
    margin: 40px auto;
  }
  
  .RESUME .text-wrapper {
    margin: 20px 0 35px 0;
  }
  
  .RESUME .resume-container {
    gap: 30px;
    padding: 0 20px;
  }
  
  .RESUME .resume-card {
    max-width: 350px;
    margin-bottom: 35px;
  }
  
  .RESUME .techImage,
  .RESUME .downloadImage {
    height: 180px;
    width: 180px;
    margin: 20px 0;
  }
  
  .RESUME .TECH-RESUME,
  .RESUME .CIVIL-PROJ {
    margin-bottom: 20px;
  }
  
  .RESUME .div,
  .RESUME .text-wrapper-2 {
    font-size: 20px;
    margin-bottom: 15px;
  }
  
  .RESUME .p {
    font-size: 16px;
    margin-top: 20px;
  }
}

/* MOBILE - Apply these styles for mobile phones */
@media screen and (max-width: 480px) {
  .RESUME {
    margin: 25px auto;
  }
  
  .RESUME .text-wrapper {
    font-size: 22px;
    margin: 15px 0 25px 0;
  }
  
  .RESUME .resume-container {
    gap: 15px;
    padding: 0 10px;
  }
  
  .RESUME .resume-card {
    max-width: 100%;
    margin-bottom: 25px;
  }
  
  .RESUME .techImage,
  .RESUME .downloadImage {
    height: 140px;
    width: 140px;
    margin: 15px 0;
  }
  
  .RESUME .TECH-RESUME,
  .RESUME .CIVIL-PROJ {
    margin-bottom: 15px;
  }
  
  .RESUME .div,
  .RESUME .text-wrapper-2 {
    font-size: 18px;
    margin-bottom: 10px;
  }
  
  .RESUME .p {
    font-size: 14px;
    line-height: 20px;
    margin-top: 15px;
  }
}