/* Global background reset */
html, 
body {
  margin: 0 !important;
  padding: 0 !important;
  overflow-x: hidden !important;
  min-height: 5150px !important;
  min-width: 100% !important;
  background-color: #000000 !important;
}

/* Pixel space background */
.pixel-space-background {
  position: fixed !important;
  top: 0 !important;
  left: 0 !important;
  width: 100% !important;
  height: 5150px !important;
  z-index: -1 !important;
  background-color: #000000 !important;
  image-rendering: pixelated !important; /* Crisp pixel rendering */
  pointer-events: none !important;
}

/* Pixel space background with high specificity */
.pixel-space-background {
  position: fixed !important;
  top: 0 !important;
  left: 0 !important;
  right: 0 !important;
  bottom: 0 !important;
  z-index: -1 !important; /* Ensure it stays behind content */
  width: 100vw !important;
  height: 5150px !important;
  pointer-events: none !important;
  
  /* GPU acceleration */
  transform: translateZ(0);
  backface-visibility: hidden;
  will-change: transform;
}

/* Full coverage canvas styling */
.pixel-space-canvas, 
canvas.pixel-space-background {
  position: fixed !important; /* Fixed position so it stays in view */
  top: 0 !important;
  left: 0 !important;
  width: 100vw !important; /* Full viewport width */
  height: 5150px !important; /* Full viewport height */
  z-index: -1 !important; /* Behind all content */
  margin: 0 !important;
  padding: 0 !important;
  pointer-events: none !important; /* Allow clicking through */
  
  /* Hardware acceleration */
  transform: translateZ(0);
  backface-visibility: hidden;
  will-change: transform;
  
  /* Prevent any scaling issues */
  object-fit: cover;
  min-width: 100vw;
  min-height: 5150px;
}

/* Canvas container (if there is one) */
.pixel-space-container {
  position: fixed !important;
  top: 0 !important;
  left: 0 !important;
  width: 100% !important;
  height: 5150px !important;
  z-index: -1 !important;
  overflow: hidden !important;
  margin: 0 !important;
  padding: 0 !important;
}

/* Make sure body has a proper background color in case of any gaps */
body {
  background-color: black !important;
}

/* Ensure render quality */
.pixel-space-canvas {
  image-rendering: -webkit-optimize-contrast;
  image-rendering: pixelated;
}

/* Prevent any background colors in other major components */
header, 
footer, 
nav, 
aside, 
article, 
div[role="main"],
div[class*="content"] {
  background-color: transparent !important;
}

/* Add media queries for different device sizes */
@media (max-width: 1200px) {
  .pixel-space-background {
    width: calc(100vw + max(500vw, 3000px)) !important;
    height: calc(100vh + max(500vh, 6000px)) !important;
  }
}

@media (max-width: 768px) {
  .pixel-space-background {
    width: calc(100vw + max(300vw, 2000px)) !important;
    height: calc(100vh + max(300vh, 6000px)) !important;
  }
}

/* For very small devices */
@media (max-width: 480px) {
  .pixel-space-background {
    width: calc(100vw + max(200vw, 1000px)) !important;
    height: calc(100vh + max(200vh, 6000px)) !important;
  }
}
